<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          :model="requestParam"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="用户ID" :required="true">
            <el-input v-model.number="requestParam.uuid"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getUC">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="success" size="mini" @click="add">新增</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <el-form
          :model="responseData"
          ref="UC"
          label-width="100px"
          class="formStyle text_common"
          style="width"
          size="mini"
          :v-show="showFrom"
        >
          <el-form-item label="用户ID" prop="uuid" :required="true">
            <el-input v-model.number="responseData.uuid"></el-input>
          </el-form-item>
          <el-form-item label="控制总目标" prop="tar" :required="true">
            <el-input v-model.number="responseData.tar"></el-input>
          </el-form-item>
          <el-form-item label="控制等级" prop="level" :required="true">
            <el-input v-model.number="responseData.level"></el-input>
          </el-form-item>
          <el-form-item label="当前进度" prop="now">
            <el-input
              v-model.number="responseData.now"
              :disabled="true"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doCommit">{{
              action.title
            }}</el-button>
            <el-button @click="resetFrom">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        uuid: null
      },
      responseData: {
        uuid: null,
        tar: null,
        level: null,
        now: null
      },
      action: {
        type: "update",
        title: "修改"
      },
      showFrom: false,
      formLabelWidth: "20%"
    };
  },
  methods: {
    getUC() {
      var that = this;
      that.responseData = [];
      this.$api.game.getUC(this.requestParam).then(res => {
        if (res.data.success === true) {
          that.responseData = res.data.data;
          that.responseData.uuid = that.requestParam.uuid;
          that.requestParam.uuid = null;
        } else {
          this.$message({
            showClose: true,
            message: "获取用户调控信息失败",
            type: "error"
          });
        }
      });
    },
    doCommit() {
      let that = this;
      this.$api.game.setUC({'uuid':this.responseData.uuid,'tar':this.responseData.tar,'level':this.responseData.level}).then(res => {
        let successMsg = that.action.type == "add" ? "新增成功!" : "修改成功!";
        let falureMsg = that.action.type == "add" ? "新增失败!" : "修改失败!";
        if (res.data.success === true) {
          this.$message({
            showClose: true,
            message: successMsg,
            type: "success"
          });
        } else {
          this.$message({
            showClose: true,
            message: falureMsg,
            type: "error"
          });
        }
      });
    },
    add() {
      this.resetFrom();
      this.action.type = "add";
      this.action.title = "新增";
    },
    resetFrom() {
      this.responseData = {
        uuid: null,
        tar: null,
        level: null,
        now: null
      };
    }
  }
};
</script>

<style lang="less" scoped>
.dialog_input {
  width: 70%;
}
.dialog_input_small {
  width: 20%;
  margin: 0 20px;
}
.formStyle {
  width: 75%;
  margin: auto auto;
  padding: 60px 0;
}
.text_common {
  color: #888;
  font-size: 13px;
}
</style>



